$torchred-to-cobalt-play:           linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
$darkcerulean-to-venetianred:       conic-gradient(from 130.87deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
$darkcerulean-tablet:               conic-gradient(from 143.87deg at 20.83% 36.26%, #b80027 -44.69deg, #003d85 32.24deg, #b80027 310.31deg, #003d85 392.24deg);
$darkcerulean-mobile:               conic-gradient(from 170.87deg at 34.2% 34.26%, #b80027 -44.69deg, #003d85 32.24deg, #b80027 310.31deg, #003d85 392.24deg);

